*{
    box-sizing: border-box;
}
.post{
    margin: 20px;
    width: 80%;
    height: auto;
    border-radius: 10px;
    padding: 2rem 1rem 1rem 1rem;
    background-color: rgb(233, 233, 233);
}
.posts{
    width: 100%;
    height: auto;
    margin-left: 5%;    
}
.post__header{
    width: 100%;
    height: 60px;
    display: flex;
    gap: 10px;
    align-items: flex-end;
}
.post__title{
    font-size: 27px;
    font-weight: 600;
    margin: 0px;
    margin-left: 0%;
}
.post__body{
    width: 100%;
    height: auto;
    margin-top: 5%;
    padding: 0px 4rem;
}
